*,
*:before,
*:after {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

body {
  font-family: 'Montserrat', 'Arial', sans-serif;
  font-weight: 400;
  background-color: #fff;
  color: #000;
}

/** Global classes **/

.visually-hidden {
  width: 1px;
  height: 1px;
  margin-top: -1px;
  margin-left: -1px;
  overflow: hidden;
  position: absolute;
  clip: rect(0, 0, 0, 0);
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.icon {
  min-width: 15px;
  min-height: 15px;
}

img,
svg {
  max-width: 100%;
  height: auto;
}

.btn {
  background-color: #5b815d;
  width: 213px;
  height: 57px;
  border-radius: 29px;
  font-weight: 600;
  color: #fffefe;
  box-shadow: 0px 7px 21px 0px rgba(55, 125, 60, 0.3);
  border: none;
  display: inline-block;
  text-align: center;
  text-decoration: none;
  line-height: 57px;
}

.btn:hover {
  transition: 0.5s;
  background-color: #97c89a;
}

/** Atomic classes **/

.d-n {
  display: none;
}

.d-b {
  display: block;
}

/** General classes **/

.header {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
}

.first_screen {
  padding-top: 185px;
  background: #fff url(../img/first_screen.jpg) no-repeat;
  background-size: cover;
  padding-bottom: 260px;
}

.navigation {
  display: flex;
  justify-content: space-between;
  padding: 45px;
  align-items: center;
}

.navigation__ul {
  display: flex;
  justify-content: flex-end;
  list-style: none;
  flex-grow: 1;
}

.navigation__li + .navigation__li {
  padding-left: 15px;
}

.navigation__li_tel {
  padding-left: 54px;
}

.navigation__link {
  text-decoration: none;
  font-size: 14px;
  color: #000;
  position: relative;
}

.navigation__link:after {
  content: '';
  display: block;
  border-bottom: 1px solid #97c89a;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  transition: width 0.3s;
}

.navigation__link:hover:after {
  width: 100%;
}

.navigation__link_tel {
  text-decoration: none;
  font-size: 14px;
  color: #000;
  position: relative;
}

.navigation__link_tel:after {
  content: '';
  display: block;
  border-bottom: 1px solid #97c89a;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  transition: width 0.3s;
}

.navigation__link_tel:hover:after {
  width: 100%;
}

.slogan__text {
  width: calc(58.333333333333336% - 20px);
  font-size: 90px;
  line-height: 1.3;
  margin-bottom: 55px;
}

.slogan__btn_white {
  margin-left: 25px;
  border-width: 2px;
  border: 2px solid #97c89a;
  background-color: transparent;
  color: #000;
}

.catalog__header {
  text-align: center;
  font-size: 48px;
  padding-top: 185px;
}

.catalog__text {
  margin: 0 auto;
  text-align: center;
  width: 570px;
  font-size: 14px;
  line-height: 21px;
  padding-top: 45px;
}

.catalog__tab {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.catalog__tab_li {
  width: calc(33.333333333333336% - 20px);
  margin: 0 auto;
  text-align: center;
  list-style-type: none;
  padding-top: 65px;
}

.catalog__tab_a {
  text-decoration: none;
  font-size: 16px;
  line-height: 21px;
  font-weight: 600;
  color: #000;
  position: relative;
}

.catalog__tab_a:hover {
  color: #97c89a;
}

.catalog__tab_a:after {
  content: '';
  display: inline-block;
  border-bottom: 1px solid #97c89a;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  transition: width 0.3s;
}

.catalog__tab_a:hover:after {
  width: 100%;
}

.catalog__ul {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.catalog__li {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(25% - 20px);
  list-style-type: none;
  padding-top: 45px;
}

.catalog__a {
  text-decoration: none;
}

.catalog__title {
  font-size: 16px;
  line-height: 21px;
  color: #000;
  text-align: center;
  padding-top: 30px;
}

.catalog__price {
  font-size: 16px;
  line-height: 21px;
  color: #5b815d;
  text-align: center;
  font-weight: 700;
  padding-top: 15px;
}

.welcome {
  padding-top: 170px;
}

.welcome__slider {
  width: 568px;
  padding-right: 20px;
  padding-bottom: 70px;
  float: left;
}

.welcome__header {
  font-size: 48px;
  padding-top: 25px;
  padding-bottom: 50px;
}

.welcome__text1 {
  font-size: 14px;
  padding-bottom: 30px;
}

.welcome__text2 {
  font-size: 14px;
  padding-top: 0px;
  padding-bottom: 70px;
}

.welcome__text3 {
  font-size: 14px;
  padding-top: 0px;
  padding-bottom: 0px;
}

.feature {
  background-color: #fff;
  margin-bottom: 45px;
}

.feature__head {
  text-align: center;
  font-size: 48px;
  margin-top: 30px;
  margin-bottom: 35px;
}

.feature__about {
  margin: 0 auto;
  text-align: center;
  font-size: 16px;
  line-height: 21px;
  width: 570px;
  padding-bottom: 85px;
}

.feature__icon {
  border-radius: 50%;
  margin-bottom: 20px;
  background-color: #eaf4eb;
  height: 75px;
  width: 75px;
  padding: 20px;
  position: relative;
  left: 100px;
}

.feature__text {
  text-align: center;
  font-weight: 600;
  font-size: 16px;
  margin: 0 auto;
}

.feature__rows {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.feature__rows .feature__card {
  margin-top: 30px;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(25% - 20px);
  margin-bottom: 25px;
  list-style-type: none;
}

.feedback__header {
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  font-size: 48px;
  padding-top: 100px;
  padding-bottom: 50px;
}

.feedback__rows {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.feedback__card {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(33.333333333333336% - 20px);
  padding: 50px 30px 60px 30px;
  background: #fff;
  border-radius: 10px;
  margin-bottom: 20px;
  list-style-type: none;
  box-shadow: 0px 1px 1px 1px rgba(0, 0, 0, 0.17);
}

.feedback__photo {
  border-radius: 50%;
}

.feedback__title {
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  font-size: 18px;
  line-height: 50px;
  padding-bottom: 10px;
  padding-top: 20px;
}

.feedback__text {
  font-size: 16px;
  line-height: 24px;
  padding-bottom: 25px;
}

.feedback__date {
  font-size: 16px;
  line-height: 24px;
  margin-top: 10px;
  color: #777777;
  font-style: italic;
}

@media screen and (max-width: 1100px) {
  .catalog__li {
    width: calc(33.333333333333336% - 20px);
  }

  .feature__rows {
    justify-content: center;
  }

  .feature__rows .feature__card {
    width: calc(25% - 20px);
  }

  .feedback__rows {
    justify-content: flex-start;
  }

  .feedback__card {
    width: calc(33.333333333333336% - 20px);
  }
}

@media screen and (max-width: 960px) {
  .catalog__li {
    width: calc(41.66666666666667% - 20px);
  }

  .feature__rows .feature__card {
    width: calc(33.333333333333336% - 20px);
  }

  .feedback__card {
    width: calc(50% - 20px);
  }
}

@media screen and (max-width: 780px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }

  .catalog__li {
    width: calc(50% - 20px);
  }

  .feature__rows .feature__card {
    width: calc(50% - 20px);
  }

  .feedback__card {
    width: calc(50% - 20px);
  }
}

@media screen and (max-width: 560px) {
  .catalog__li {
    width: calc(100% - 20px);
  }

  .feature__rows .feature__card {
    width: calc(100% - 20px);
  }

  .feedback__rows {
    justify-content: flex-start;
  }

  .feedback__card {
    width: calc(100% - 20px);
  }
}